.Tetris {
  position: relative;
  display: flex;
  padding: 2rem;
  background: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin: 2rem auto;
  max-width: 800px;
  min-height: 600px;
}

.Tetris::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, #3498db, #2ecc71);
  opacity: 0.1;
  border-radius: 10px;
  z-index: -1;
}

@media (max-width: 768px) {
  .Tetris {
    padding: 1rem;
    margin: 1rem;
    min-height: 500px;
  }
}
